
Template.__checkName("MeteorToys");
Template["MeteorToys"] = new Template("Template.MeteorToys", (function() {
  var view = this;
  return Blaze.If(function() {
    return Spacebars.call(view.lookup("MeteorToys"));
  }, function() {
    return [ "\n\n		", HTML.Comment(" Render Meteor Toys "), "\n		", Blaze.If(function() {
      return Spacebars.call(view.lookup("MeteorToys_Pro"));
    }, function() {
      return [ "\n			", Spacebars.include(view.lookupTemplate("MeteorToys_tooltip")), "\n			", HTML.DIV({
        "class": "MeteorToys_orbs"
      }, "\n				", HTML.Comment(" Render Toys "), "\n				", Blaze.Each(function() {
        return Spacebars.call(view.lookup("MeteorToy"));
      }, function() {
        return [ "\n					", Blaze._TemplateWith(function() {
          return {
            template: Spacebars.call(view.lookup("."))
          };
        }, function() {
          return Spacebars.include(function() {
            return Spacebars.call(Template.__dynamic);
          });
        }), "\n				" ];
      }), "\n				", HTML.Comment(" Render Addons "), "\n				", Blaze.Each(function() {
        return Spacebars.call(view.lookup("MeteorToy_addon"));
      }, function() {
        return [ "\n					", HTML.Comment(" {{#MeteorToy name=this}} "), "\n						", Blaze._TemplateWith(function() {
          return {
            template: Spacebars.call(view.lookup("."))
          };
        }, function() {
          return Spacebars.include(function() {
            return Spacebars.call(Template.__dynamic);
          });
        }), "\n					", HTML.Comment(" {{/MeteorToy}} "), "\n				" ];
      }), "\n				", HTML.Comment(" End "), "\n			"), "\n			", HTML.Comment(" {{> MeteorToys_notifications}} "), "\n		" ];
    }, function() {
      return [ "\n			", HTML.DIV({
        "class": "MeteorToys_orbs"
      }, "\n				", Blaze._TemplateWith(function() {
        return {
          template: Spacebars.call(view.lookup("all"))
        };
      }, function() {
        return Spacebars.include(function() {
          return Spacebars.call(Template.__dynamic);
        });
      }), "\n				", Blaze.Each(function() {
        return Spacebars.call(view.lookup("MeteorToy_addon"));
      }, function() {
        return [ "\n					", HTML.Comment(" {{#MeteorToy name=this}} "), "\n						", Blaze._TemplateWith(function() {
          return {
            template: Spacebars.call(view.lookup("."))
          };
        }, function() {
          return Spacebars.include(function() {
            return Spacebars.call(Template.__dynamic);
          });
        }), "\n					", HTML.Comment(" {{/MeteorToy}} "), "\n				" ];
      }), "\n			"), "\n		" ];
    }), "\n\n		", HTML.Comment(" Render Non-Toy Packages "), "\n		", HTML.Comment(" ie. Mongol, JetSetter "), "\n		", Blaze.Each(function() {
      return Spacebars.call(view.lookup("MeteorToysPackage"));
    }, function() {
      return [ "\n			", Blaze._TemplateWith(function() {
        return {
          template: Spacebars.call(view.lookup("."))
        };
      }, function() {
        return Spacebars.include(function() {
          return Spacebars.call(Template.__dynamic);
        });
      }), "\n		" ];
    }), "\n		", HTML.Comment(" End "), "\n	" ];
  }, function() {
    return [ "\n		", Spacebars.include(view.lookupTemplate("MeteorToys_notification_widget")), "\n	" ];
  });
}));

